import { h } from 'preact';
import BarChart from './../../universal/BarChart/index.jsx';


export default function NewHomeChart(props) {
  const {
    items,
    width,
    mobile,
    hasNull,
  } = props;

  const { parentAction } = props;

  const noValues = (
    <ul className="u-margin0 u-paddingLeft20">
      {Object.keys(items).map(val => <li>{val}</li>)}
    </ul>
  );

  const withValues = (
    <BarChart
      name="programmes-chart"
      guides={!mobile}
      hover={!mobile}
      {...{ width, parentAction, items }}
    />
  );


  return hasNull ? noValues : withValues;
}



